export default {
	data() {
		return {
			type: 'password',
			isuser: 0, //用户名是否占用
			isemail: 0, //邮箱是否占位
			show: false, // 是否显示密码
			invalid: false, // 邮箱是否符合
			employ: false, //是否被占用
			showurl: '../../static/images/register/showin.png',
			email: '',
			name: ''
		};
	},
	methods: {
		//密码是否显隐
		showon: function() {
			if (this.show) {
				this.type = 'password';
				this.show = !this.show;
				this.showurl = '../../static/images/register/showin.png';
			} else {
				this.type = 'text';
				this.show = !this.show;
				this.showurl = '../../static/images/register/showon.png';
			}
		},
		isname: function() {
			if (this.name.length > 0) {
				this.isuser = true;
			} else {
				this.isuser = false;
			}
		},
		goLogin: function() {
			uni.navigateBack({
				delta: 1
			});
		}
	}
};
